import React, { Component } from 'react';
import Vacation from './Vacation';
import Chart from './Chart';
import Header from './Header';
import { Col , Row , Card , CardBody , CardFooter , CardTitle , CardSubtitle , CardText , Label } from 'reactstrap';
import { getSocket } from '../helpers/helpers';
import './Connected.css';
import { DATABASE_URL } from '../helpers/database';


import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

var _ = require('lodash');




class Connected extends Component {
    
    newVacation=<div></div>;
    vacationsChart=<div></div>;

    constructor(props){
        super(props);
        this.state = { vacations:[] , newVacation:{ destination:'' , arrival:'' , returndate:'' , description:'' , price:'' , picture:'' } };
    }

    // get all the vacation after the render and subscribe to the socket
    componentDidMount(){
        this.getAllVacation();
        let that = this
        if(this.props.user.status=='admin'){
                this.newVacation = <Col sm="3">
                                    <Card onClick={this.addVacation.bind(this)}>
                                      <CardBody>
                                          <img src="https://vignette.wikia.nocookie.net/i-shall-seal-the-heavens/images/2/2f/Plus.png/revision/latest?cb=20180221183139" width="100%" />
                                      </CardBody>
                                    </Card>
                                 </Col>;
                this.vacationsChart = <Chart></Chart>
        }

        getSocket().emit('connection');

        getSocket().on('connection', function() {
            console.log('new user connected');
        });

        getSocket().on('update', function() {
            console.log('get vacation update');
            that.getAllVacation();
        });
        
    }
    
    // get all the vacations from database and update the state
    getAllVacation() {
        
        let followVacations = this.props.user.vacations;
        let dataFollow = [];
        let that = this;
        fetch(DATABASE_URL+'/vacations')
            .then(response => response.json()).then((data) =>{
                // sort the data
                data.map((vacation) => {
                    followVacations.map((fv) => {
                        if(fv == vacation.id) {
                            dataFollow.push(vacation);
                        }
                    });
                });
            let dataNoFollow = _.pullAll(data,dataFollow);
            dataNoFollow.forEach((v) => v.checked=false);
            dataFollow.forEach((v) => v.checked=true);
            let newData = _.concat(dataFollow, dataNoFollow);
            let newState = Object.assign({},that.state);
            newState.vacations = newData;
            that.setState(newState);  
            });
    }

    // call when the form inputs change, update the 'newVacation' state
    hundleChange(date, e) {
        let newState = Object.assign({},this.state);
        if(!e.target){
            switch (date) {
                case 'arrival':
                    newState.newVacation.arrival = e;
                    break;
                case 'return':
                    newState.newVacation.returndate = e;
                    break;
            }
        }
        else {
            newState.newVacation[e.target.name] = e.target.value;
        }
        this.setState(newState);
        this.addVacation();
    }

    // save the new vacation in the database
    saveVacation(){
        let newVacation = this.state.newVacation;
        newVacation.arrival = newVacation.arrival.toLocaleDateString();
        newVacation.returndate = newVacation.returndate.toLocaleDateString();
        fetch(`${DATABASE_URL}/vacations/`, {
                        method: 'POST',
                        headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json',
                        },
                        body: JSON.stringify(newVacation)
                        }).then(data => {
                            // this.getAllVacation();
                            getSocket().emit('update');
                            this.newVacation= <Col sm="3">
                                  <Card onClick={this.addVacation.bind(this)}>
                                      <CardBody>
                                          <img src="https://vignette.wikia.nocookie.net/i-shall-seal-the-heavens/images/2/2f/Plus.png/revision/latest?cb=20180221183139" width="100%" />
                                      </CardBody>
                                  </Card>
                                 </Col>;
                            let newState = Object.assign({},this.state);
                            newState.newVacation = { destination:'' , arrival:'' , returndate:'' , description:'' , price:'' , picture:'' };
                            this.setState(newState); 
                        });
    }

    // change the plus card to new vacation form
    addVacation(){
        this.newVacation = <Col sm="3"><Card>
                                <CardBody>
                                    <CardTitle><input type="text" name="destination" onChange={this.hundleChange.bind(this,'')} value={this.state.newVacation.destination} placeholder="Enter destination" /></CardTitle>
                                    <CardSubtitle><DatePicker onChange={this.hundleChange.bind(this,'arrival')} selected={this.state.newVacation.arrival} placeholderText="Enter date arrival" /> - 
                                    <DatePicker onChange={this.hundleChange.bind(this,'return')} selected={this.state.newVacation.returndate} placeholderText="Enter date return" /></CardSubtitle>
                                </CardBody>
                                <input type="url" name="picture" onChange={this.hundleChange.bind(this,'')} value={this.state.newVacation.picture} placeholder="Enter picture url" />
                                <CardBody>
                                    <CardText><input type="text" name="description" onChange={this.hundleChange.bind(this,'')} value={this.state.newVacation.description} placeholder="Enter description" /></CardText>
                                </CardBody>
                                <CardFooter className="text-muted">
                                    <Label><input type="number" name="price" onChange={this.hundleChange.bind(this,'')} value={this.state.newVacation.price} placeholder="Enter price" />$</Label>
                                    <input type="button" value="save" onClick={this.saveVacation.bind(this)} />
                                </CardFooter>
                            </Card></Col>;
        let newState = Object.assign({},this.state);
        this.setState(newState);
    }

  render() {
 
    return (
      <div >
          <Header user={this.props.user} logout={this.props.logout.bind(this)} />
          <Row>
            {
                this.state.vacations.map((vacation) => <Col sm="3"><Vacation data={vacation} user={this.props.user} /></Col>)
            }
            {this.newVacation}
            
          </Row>
          {/* <div> */}
              {this.vacationsChart}
          {/* </div> */}
      </div>
    );
  }
}

export default Connected;
