import React, { Component } from 'react';
import follow from '../follow-icon.png';
import noFollow from '../no-follow-icon.png';

class Follow extends Component {

  constructor(props)
  {
      super(props);
  }

  render() {
    if(this.props.checked){
      return (
      <img src={follow} onClick={this.props.clickFunc.bind(this)} height="30" />
      );
    }
    else {
      return (
        <img src={noFollow} onClick={this.props.clickFunc.bind(this)} height="30" />
      );
    }   
  }
}

export default Follow;
