import React, { Component } from 'react';
import {Bar} from 'react-chartjs-2';
import { getSocket } from '../helpers/helpers';
import { DATABASE_URL } from '../helpers/database';

class Chart extends Component {

    chartOptions = {
        scales: {
            yAxes: [{
                ticks: {
                    beginAtZero:true
                }
            }]
        }
    };

  constructor(props)
  {
      super(props);
      this.state = {
          chartData: {
            labels: [],
            datasets: [{
                label: "Follow",
                backgroundColor: 'rgb(255, 99, 132)',
                borderColor: 'rgb(255, 99, 132)',
                data: [],
            }]
        }
    }
  }

  componentDidMount() {
    this.getData();

    getSocket().on('update', () => {
        console.log('get vacation update');
        this.getData();
    });
  }

  getData() {
      let chartDetails = Object.assign({},this.state.chartData);
    fetch(DATABASE_URL+"/vacations")
            .then(response => response.json()).then((data) => {
                chartDetails.labels = [];
                chartDetails.datasets[0].data = [];
                data.map((vacation) => {
                    if(vacation.following>0){
                        chartDetails.labels.push(`#${vacation.id} ${vacation.destination}`);
                        chartDetails.datasets[0].data.push(vacation.following.toString());
                    }
                });
                console.log(chartDetails);
            this.setState({ chartData: chartDetails });
            });
  }

  render() {
    return (
        <Bar data={this.state.chartData} options={this.chartOptions} height={100} />
    );
  }
}

export default Chart;
