import React, { Component } from 'react';
import { Card , CardBody , CardTitle , CardSubtitle , CardText , CardFooter , Label } from 'reactstrap';
import './Vacation.css';
import { getSocket , connectSocket } from '../helpers/helpers';
import Follow from './Follow';
import { DATABASE_URL } from '../helpers/database';

import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
var moment = require('moment');


class App extends Component {

    constructor() {
        super();
        this.state = {
                       edit: false,   //true- put the vacation details into inputs for edit, false- normal card
                       editVacation:{ destination:'' , arrival:'' , returndate:'' , description:'' , price:'' , picture:'' } // the editted vacation details
                      };
      }

      componentDidMount() {
      }
     
      // increase/decrease number followers and change the checked state
      changeFollow() {
        // update the vacation followers in the database (increase)
        if(!this.props.data.checked){
          this.increaseFollow();
        }

        // update the vacation followers in the database (decrease)
        else {
          this.decreaseFollow();
        }
      }

      // increase the vacation number follow and add to the user vacations
      increaseFollow() {
        let user = Object.assign( {} , this.props.user);
          let arr = user.vacations;
          arr.push(this.props.data.id);
          let arrString = `[${arr.toString()}]`;

          // update the following vacations of the user
            fetch(`${DATABASE_URL}/users/${this.props.user.id}`, {
            method: 'PUT',
            headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            },
            body: JSON.stringify({vacations: arrString})
            }).then(data => {
              getSocket().emit('userUpdated',this.props.user.id);
            });

          let num = this.props.data.following;
          num++;
          // update the follow number of the vacation
          fetch(`${DATABASE_URL}/vacations/follow/${this.props.data.id}`, {
            method: 'PUT',
            headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            },
            body: JSON.stringify({follow: num})
            }).then(data => {
              getSocket().emit('update');
            });
      }

      // decrease the vacation number follow and remove from the user vacations
      decreaseFollow() {
        let user = Object.assign( {} , this.props.user);
          let arr = user.vacations;
          let index = arr.findIndex((i) => i==this.props.data.id);
          arr.splice(index,1);
          let arrString = `[${arr.toString()}]`;

          // update the following vacations of the user
          fetch(`${DATABASE_URL}/users/${this.props.user.id}`, {
            method: 'PUT',
            headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            },
            body: JSON.stringify({vacations: arrString})
            }).then(data => {
              getSocket().emit('userUpdated',this.props.user.id);
            });

          let num = this.props.data.following;
          num--;
          fetch(`${DATABASE_URL}/vacations/follow/${this.props.data.id}`, {
            method: 'PUT',
            headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            },
            body: JSON.stringify({follow: num})
            }).then(data => {
              getSocket().emit('update');
              
              // this.props.getVacations();
            });
      }

      // call when the form inputs change, update the 'editVacation' state
      hundleChange(date, e) {
        let newState = Object.assign({},this.state);
        if(!e.target){
            switch (date) {
                case 'arrival':
                    newState.editVacation.arrival = e;
                    break;
                case 'return':
                    newState.editVacation.returndate = e;
                    break;
            }
        }
        else {
            newState.editVacation[e.target.name] = e.target.value;
        }
        this.setState(newState);

        // let newState = Object.assign({},this.state);
        // newState.editVacation[e.target.name] = e.target.value;
        // this.setState(newState);
      }

      // change the edit state to true (put the vacation details into inputs element)
      changeToEdit() {
        let newState = Object.assign({},this.state);
        newState.edit = true;
        let vacationData = this.props.data;
        vacationData.arrival = new moment(vacationData.arrival)._d;
        vacationData.returndate = new moment(vacationData.returndate)._d;
        newState.editVacation = vacationData;
        this.setState(newState);
      }

      // save the edit vacation in the database
      setUpdate() {
        let editVacation = this.state.editVacation;
        editVacation.arrival = editVacation.arrival.toLocaleDateString();
        editVacation.returndate = editVacation.returndate.toLocaleDateString();
        fetch(`${DATABASE_URL}/vacations/${this.props.data.id}`, {
            method: 'PUT',
            headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            },
            body: JSON.stringify(editVacation)
            }).then(data => {
              getSocket().emit('update');

              // change the edit state to false
              let newState = Object.assign({},this.state);
              newState.editVacation = { destination:'' , arrival:'' , returndate:'' , description:'' , price:'' , picture:'' };
              newState.edit = false;
              this.setState(newState);
            });
      }

      // delete vacation from database
      deleteVacation() {
        fetch(`${DATABASE_URL}/vacations/${this.props.data.id}`, {
            method: 'DELETE',
            headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            },
            body: JSON.stringify()
            }).then(data => {
              getSocket().emit('update');

              // get all the vacations again
              // this.props.getVacations();
            });
      }

  render() {
    // user connect
    if(this.props.user.status=='user'){
      return (
      <div className="card">
        <Card>
        <CardBody>
          <i className="follow">
          <Follow checked={this.props.data.checked} clickFunc={this.changeFollow.bind(this)} ></Follow>
          </i>
          <CardTitle>{this.props.data.destination}</CardTitle>
          <CardSubtitle>{this.props.data.arrival} - {this.props.data.returndate}</CardSubtitle>
        </CardBody>
        <img width="100%" src={this.props.data.picture} alt="No image" />
        <CardBody>
          <CardText>{this.props.data.description}</CardText>
        </CardBody>
        <CardFooter className="text-muted">
          <Label>{this.props.data.price}$</Label>
          <span><img src="http://www.iconarchive.com/download/i95993/iconsmind/outline/Eye-2.ico" height="20" />{this.props.data.following}</span>
          </CardFooter>
      </Card>
      </div>
    );
    }
    // admin connect
    else if(this.props.user.status=='admin') {
      // edit vacation mode
      if(this.state.edit){
        return (
          <div>
            <Card>
            <CardBody>
            <i className="editBtn"><img src="http://land.be-inspiration.com/Img/v.png" height="20" onClick={this.setUpdate.bind(this)} /></i>
              <CardTitle><input type="text" name="destination" onChange={this.hundleChange.bind(this,'')} value={this.state.editVacation.destination} /></CardTitle>
              <CardSubtitle><DatePicker onChange={this.hundleChange.bind(this,'arrival')} selected={this.state.editVacation.arrival}/> -
               <DatePicker onChange={this.hundleChange.bind(this,'return')} selected={this.state.editVacation.returndate} /></CardSubtitle>
            </CardBody>
            <input type="url" name="picture" onChange={this.hundleChange.bind(this,'')} value={this.state.editVacation.picture} />
            <CardBody>
              <CardText><input type="text" name="description" onChange={this.hundleChange.bind(this,'')} value={this.state.editVacation.description} /></CardText>
            </CardBody>
            <CardFooter className="text-muted">
              <Label><input type="number" name="price" onChange={this.hundleChange.bind(this,'')} value={this.state.editVacation.price} />$</Label>
              <span><img src="http://www.iconarchive.com/download/i95993/iconsmind/outline/Eye-2.ico" height="20" />{this.props.data.following}</span>
              </CardFooter>
          </Card>
          </div>
          );
      }
      // admin connect (not edit mode)
      else {
        return (
        <div>
          <Card>
          <CardBody>
            <i className="editBtn"><img  src="http://cdn.onlinewebfonts.com/svg/img_386644.png" height="20" onClick={this.changeToEdit.bind(this)} /></i>
            <i className="deleteBtn"><img  src="https://www.wineroute.co.il/img/x-icon.png" height="20" onClick={this.deleteVacation.bind(this)} /></i>
            <CardTitle>{this.props.data.destination}</CardTitle>
            <CardSubtitle>{this.props.data.arrival} - {this.props.data.returndate}</CardSubtitle>
          </CardBody>
          <img width="100%" src={this.props.data.picture} alt="No image" />
          <CardBody>
            <CardText>{this.props.data.description}</CardText>
          </CardBody>
          <CardFooter className="text-muted">
            <Label>{this.props.data.price}$</Label>
            <span><img src="http://www.iconarchive.com/download/i95993/iconsmind/outline/Eye-2.ico" height="20" />{this.props.data.following}</span>
            </CardFooter>
        </Card>
        </div>
        );
      }    
    }

  }
}

export default App;