import React, { Component } from 'react';
import Login from './Login';
import Connected from './Connected';


class Main extends Component {
    constructor(props){
        super(props);
        this.state = { currentUser:{id:'', username:'', name:'', status:'', vacations:[]} };
    }

    userLogged(user){
        let userConnect = 
        {
            id: user.id,
            username: user.username,
            name: user.name,
            status: user.status,
            vacations: JSON.parse(user.vacations)
        }
        this.setState({ currentUser: userConnect });
    }

    userLogout(){
        this.setState({ currentUser:{id:'', username:'', name:'', status:''} });
    }

  render() {
    if(this.state.currentUser.id!=''){
        return <Connected user={this.state.currentUser} logout={this.userLogout.bind(this)} />
    }
    else {
        return <Login userLogged={this.userLogged.bind(this)} />
    }
  }
}

export default Main;
