import React, { Component } from 'react';
import { TabContent, TabPane, Nav, NavItem, NavLink, Row, Col, Form, FormGroup, Label, Input, Container, Badge, Alert } from 'reactstrap';
import classnames from 'classnames';
import { getSocket, connectSocket } from '../helpers/helpers';
import './Login.css';
import { DATABASE_URL } from '../helpers/database';


class Login extends Component {
    constructor(props) {
        super(props);
    
        this.toggle = this.toggle.bind(this);
        this.state = {
          activeTab: '1',
          user: { name:'', lastname:'', username:'', password:'' },
          loginAlert:'',
          registerAlert:''
        };
      }

      componentDidMount() {
          getSocket().on('userUpdated', (id) => {
            console.log('get user update')
            debugger;
            this.buttonClick('updateuser',id);
          });
      }
    
      toggle(tab) {
        if (this.state.activeTab !== tab) {
          this.setState({
            activeTab: tab
          });
        }
      }

      hundleChange(e) {
        let newState = Object.assign({}, this.state); 
        newState.user[e.target.name] = e.target.value;
        this.setState(newState);
      }

      buttonClick(type,id){
        let myState = Object.assign({}, this.state); 
        switch (type) {
            case 'login':
                fetch(`${DATABASE_URL}/users/byUsername/${this.state.user.username}`)
                .then(response => response.json()).then((data) =>{
                    if(data.length>0 && data[0].username == this.state.user.username){
                        if(data[0].password == this.state.user.password) {
                            this.props.userLogged(data[0]);
                        }
                        else{
                            myState.loginAlert = <Alert color="danger">Password Wrong!</Alert>
                        }
                    }
                    else{
                        myState.loginAlert = <Alert color="danger">User not exist!</Alert> ;
                    }
                    this.setState(myState);
                });
                break;
            case 'register':
                // init the inputs class
                let inputElements = document.getElementsByTagName('Input');
                for(let i=0; i<inputElements.length; i++)
                    inputElements[i].classList.remove("required");

                // check the inputs not empty
                if(this.state.user.name=='' || this.state.user.lastname=='' || this.state.user.username=='' || this.state.user.password==''){
                    if(this.state.user.name==''){
                        document.getElementsByClassName("name")[0].classList.add("required");
                    }
                    if(this.state.user.lastname==''){
                        document.getElementsByClassName("lastname")[0].classList.add("required");
                    }
                    if(this.state.user.username==''){
                        document.getElementsByClassName("username")[0].classList.add("required");
                    }
                    if(this.state.user.password==''){
                        document.getElementsByClassName("password")[0].classList.add("required");
                    }
                }
                // all the inputs filled
                else {
                    // init the inputs class
                    let inputElements = document.getElementsByTagName('Input');
                    for(let i=0; i<inputElements.length; i++)
                        inputElements[i].classList.remove("required");
                    console.log(DATABASE_URL);
                    fetch(`${DATABASE_URL}/users/byUsername/${this.state.user.username}`)
                    .then(response => response.json()).then((data) =>{
                        // check if the username allready exist
                        if(data.length>0 && data[0].username == this.state.user.username){
                            document.getElementsByClassName("username")[0].classList.add("required");
                            myState.registerAlert = <Alert color="danger">User allready exist!</Alert>
                            this.setState(myState);
                        }
                        // add new user 
                        else{
                            let newUser = {
                                username: this.state.user.username,
                                name: `${this.state.user.name} ${this.state.user.lastname}`,
                                password: this.state.user.password
                            }
                            fetch(DATABASE_URL, {
                            method: 'POST',
                            headers: {
                            'Accept': 'application/json',
                            'Content-Type': 'application/json',
                            },
                            body: JSON.stringify(newUser)
                            }).then(data => {
                                myState.registerAlert = <Alert color="success">Congratulations! You just registered to our Flight Site, now you can log in</Alert>
                                this.setState(myState);
                            });
                        }
                    });
                break;
                }



                
            case 'updateuser':
                fetch(`${DATABASE_URL}/users/${id}`)
                .then(response => response.json()).then((data) => {
                    this.props.userLogged(data[0]);
                });
                break;
        }
      }

      render() {
        return (
          <Container>
            <Nav tabs>
              <NavItem>
                <NavLink
                  className={classnames({ active: this.state.activeTab === '1' })}
                  onClick={() => { this.toggle('1'); }}
                >
                  Log In
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  className={classnames({ active: this.state.activeTab === '2' })}
                  onClick={() => { this.toggle('2'); }}
                >
                  Sign Up
                </NavLink>
              </NavItem>
            </Nav>
            <TabContent activeTab={this.state.activeTab}>
              <TabPane tabId="1">
                <Row>
                  <Col sm="12">
                        <Form>
                            {this.state.loginAlert}
                            <FormGroup row>
                                <Label for="username" sm={2}>Username: </Label>
                                <Col sm={10}>
                                    <Input type="text" name="username" value={this.state.user.username} onChange={this.hundleChange.bind(this)} />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label for="password" sm={2}>Password: </Label>
                                <Col sm={10}>
                                    <Input type="password" name="password" value={this.state.user.password} onChange={this.hundleChange.bind(this)} />
                                </Col>
                            </FormGroup>
                            <Input type="button" value="SIGN IN" onClick={this.buttonClick.bind(this,'login')} />
                        </Form>
                        <Badge onClick={() => { this.toggle('2'); }} color="primary">New user? register</Badge>
                  </Col>
                </Row>
              </TabPane>
              <TabPane tabId="2">
                <Row>
                  <Col sm="12">
                            <Form>
                                {this.state.registerAlert}
                                <FormGroup row>
                                    <Label for="name" sm={2}>Name: </Label>
                                    <Col sm={10}>
                                        <Input type="text" className="name" name="name" value={this.state.user.name} onChange={this.hundleChange.bind(this)} />
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                    <Label for="lastname" sm={2}>Last name: </Label>
                                    <Col sm={10}>
                                        <Input type="text" className="lastname" name="lastname" value={this.state.user.lastname} onChange={this.hundleChange.bind(this)} />
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                <Label for="username" sm={2}>Username: </Label>
                                <Col sm={10}>
                                    <Input type="text" className="username" name="username" value={this.state.user.username} onChange={this.hundleChange.bind(this)} />
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label for="password" sm={2}>Password: </Label>
                                <Col sm={10}>
                                    <Input type="password" className="password" name="password" value={this.state.user.password} onChange={this.hundleChange.bind(this)} />
                                </Col>
                            </FormGroup>
                            <Input type="button" value="SIGN UP" onClick={this.buttonClick.bind(this,'register')}  />
                            </Form>
                  </Col>
                </Row>
              </TabPane>
            </TabContent>
          </Container>
        );
      }
}

export default Login;
